/* src/App.css */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: linear-gradient(180deg, #007bff, #0056b3);
  color: #fff;
  display: flex;
  flex-direction: column;
}

header {
  background-color: #007bff;
  padding: 15px;
  text-align: center;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

h1 {
  margin: 0;
}

main {
  width: 100%;
  max-width: 600px;
  padding: 20px;
  box-sizing: border-box;
  flex-grow: 1; /* Expand to fill available space */
}

textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
}

button {
  padding: 10px;
  background-color: #0056b3;
  color: #fff;
  border: none;
  cursor: pointer;
  width: 100%;
}

button:hover {
  background-color: #004080;
}

.error-message {
  color: #ff0000;
  margin-top: 10px;
}

img {
  max-width: 100%;
  height: auto;
  margin-top: 10px;
}

/* Footer adjustments */
footer {
  margin-top: auto;
  padding: 10px;
  background-color: #004080;
  color: #fff;
  text-align: center;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
